@import '~bootstrap/scss/mixins';
@import '~bootstrap/scss/bootstrap-grid';
@import '~bootstrap/scss/utilities/flex';
@import '~@fancyapps/fancybox/dist/jquery.fancybox.css';
@import '~aos/dist/aos.css';
@import 'fonts';
@import 'header.scss';
@import 'banner-main.scss';
@import 'curtains-types.scss';
@import 'curtains-popup.scss';
@import 'instagram.scss';
@import 'contacts.scss';
@import 'footer.scss';

html,body {
	height: 100%;
}

html,body,p,h1 {
	margin: 0;
	padding: 0;
	font-family: 'HelveticaNeueCyr', sans-serif;
	font-weight: 200;
}
ul{
	list-style: none;
	padding-left: 15px;
	margin: 5px auto;
}
a {
	text-decoration: none;
	color: #000;
}
section{
	max-width: 968px;
	margin: 0 auto;
	padding-left:15px;
	padding-right:15px;
	@include media-breakpoint-up(sm) {
		padding-left:25px;
		padding-right:25px;
	}
	@include media-breakpoint-up(lg) {
		padding-left:0;
		padding-right:0;
	}

}
body {
	display: flex;
	flex-direction: column;
}
.content {
	flex-grow: 1;
}
.section-wrapper{
	&--wide{
		&--black{
			background-color: #000000;
			max-width: none;
		}
	}
}
//TODO fix title magins
.page-content{
	padding-top:1px;
}

.content {
	font-size:16px;
	line-height: 1.3;
}

.section-title{
	margin: 23px auto;
	font-size:25px;
	font-weight:300;
	text-align: center;
	overflow: hidden;
	@include media-breakpoint-up(lg) {
		font-size:31px;
	}
	&:before,&:after{
		content: "";
		height:1px;
		position: relative;
		width:100%;
		display: inline-block;
		background-color: #000000;
	}
	&:before{
		margin-left: -100%;
		margin-right: 10px;
		left: -14px;
		top:-6px;
	}
	&:after {
		margin-right: -100%;
		margin-left: 10px;
		right: -14px;
		top:-6px;
	}
	&--white{
		@extend .section-title;
		color: #ffffff;
		&:before,&:after{
			background-color: #ffffff;
		}
	}
}
