.banner-main {
	background-image: url('../img/main-banner-small.png');
	max-width: 968px;
	height: 146px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	padding-left:0;
	padding-right:0;
	@include media-breakpoint-up(sm) {
		height: 189px;
	}
	@include media-breakpoint-up(md) {
		background-image: url('../img/main-banner-big.png');
		height: 316px;
	}
	@include media-breakpoint-up(lg) {
		height: 416px;
	}
	&__text{
		background-color: rgba(255,255,255,0.7);
		text-align: center;
		font-size:20px;
		padding:5px 0;
		@include media-breakpoint-up(sm) {
			padding:10px 0;
			font-size:25px;
		}
		@include media-breakpoint-up(md) {
			font-size:40px;
			padding:15px 0;
		}
		span{
			font-weight:700;
		}
	}
}