header{
	width:100%;
	z-index: 1;
}

.header--fixed{
	position: fixed;
}

.header-info {
	height: 100%;
	padding-left: 15px;
	padding-right: 10px;
	background-color: #fff;
	@include media-breakpoint-up(sm) {
		height: 70px;
		padding:0 25px;
	}
	@include media-breakpoint-up(lg) {
		height: 105px;
		padding: 0;
	}
	&__wrapper {
		max-width: 968px;
		margin: auto;
		height:100%;
	}
	&__mobile-menu {
		img {
			width: 35px;
			height: auto;
			margin-bottom:-13px;
			@include media-breakpoint-up(sm) {
				margin-bottom:-3px;
				width: 55px;
			}
		}
	}
	&__title-mobile{
		text-align: center;
		padding-bottom: 5px;
	}
	&__logo-card {
		font-size: 33px;
		margin: 0;
		@include media-breakpoint-up(sm) {
			font-size: 18px;
			margin-left: 70px;
		}
		@include media-breakpoint-up(lg) {
			margin: 0;
		}
		&__text {
			margin-left: 15px;
		}
		&__company-name {
			font-size: 23px;
			font-weight: 700;
			margin-bottom: -8px;
			@include media-breakpoint-up(lg) {
				font-size: 33px;
			}
		}
		&__title {
			font-size: 15px;
			font-weight: 200;
			@include media-breakpoint-up(lg) {
				font-size: 23px;
			}
		}
	}
	&__logo {
		margin-top: 5px;
		height: 60px;
		@include media-breakpoint-up(lg) {
			height: auto;
			transition: height 0.3s ease;
		}
	}
	&__phones {
		padding:10px;
		font-size: 14px;
		a {
			font-weight: 500;
		}
		@include media-breakpoint-up(sm) {
			padding:0;
			font-size: 18px;
		}
		@include media-breakpoint-up(lg) {
			font-size: 20px;
		}
	}
	&__address {
		font-weight: 500;
		font-size: 20px;
		a{
			text-decoration: underline;
		}
	}

}

.top-menu {
	background-color: #000;
	&__list {
		height: 40px;
		list-style: none;
		max-width: 968px;
		margin: auto;
	}
	&__item {
		color: #ffffff;
		text-transform: lowercase;
		font-size: 25px;
		font-weight: 200;
		padding-bottom: 4px;
		a {
			color: #fff;
		}
	}
}
.mobile-menu{
	background-color: #fff;
	position: fixed;
	left:-420px;
	top:0;
	z-index: 2;
	max-width: 420px;
	min-width: 320px;
	height:100%;
	border-right:1px #000 solid;
	&__list{
		margin-top:30px;
		list-style: none;
		text-align: center;
		padding:0;
	}
	&__item {
		border-bottom:1px #000 solid;
		a{
		display: block;
		font-size: 22px;
		font-weight: 200;
		padding:20px 40px;
		text-transform: lowercase;
			&:hover{
				background-color: #000;
				color: #fff;
			}
		}
		&:first-child{
			border-top:1px #000 solid;
		}
	}
	&__logo-card {
		font-size: 33px;
		margin: 0;
		justify-content: center;
		margin-top:40px;
		&__text {
			margin-left: 15px;
		}
		&__title {
			font-size: 15px;
			font-weight: 200;
		}
		&__logo {
			margin-top: 5px;
			height: 60px;
		}
		&__company-name {
			font-size: 23px;
			font-weight: 700;
			margin-bottom: -8px;
		}
	}
	&__close{
		position: absolute;
		top:0;
		right:0;
		height: 55px;
		width: 55px;
		img{
			height: 20px;
			width: 20px;
		}
	}
}

