.curtains-types {
	&__card {
		padding: 0 2px;
		margin-top: 10px;
		&__content {
			padding: 10px 15px;
			border: 1px #000 solid;
			border-radius: 15px;
			width: 75%;
			@include media-breakpoint-up(sm) {
				width: 100%;
			}
		}
		&__description {
			text-align: justify;
			min-height: 160px;
		}
		&__title {
			text-align: center;
			font-size: 20px;
		}
		&__link {
			font-weight: 500;
			margin-top: 10px;
			text-decoration: underline;
		}
		img {
			width: 200px;
			height: 209px;
			border-radius: 15px;
		}
	}
}