.instagram {
	padding-top: 1px;
	padding-bottom: 10px;
	margin-top: 20px;
	margin-bottom: 20px;
	&__widget-wrapper {
		margin: 0 25px;
		@include media-breakpoint-up(lg) {
			margin: 0 45px;
		}
		img {
			width: 206px;
			height: auto;
		}
	}
	&__link {
		margin-top: 15px;
		@include media-breakpoint-up(lg) {
			margin-left: 100px;
		}
		a {
			font-size: 20px;
			color: #ffffff;
			margin-left: 10px;
			@include media-breakpoint-up(md) {
				font-size: 23px;
			}
			@include media-breakpoint-up(lg) {
				font-size: 31px;
			}
		}
	}
}