.contacts {
	&__wrapper{
		@include media-breakpoint-up(md){
			margin: 0 50px 25px;
		}
	}
	&__details{
		font-size:19px;
		font-weight:500;
		@include media-breakpoint-up(lg){
			font-size:25px;
		}
		p{
			margin-left:20px;
		}
		&__item{
			margin-bottom:45px;
			padding:0;
		}
		&__item:last-child{
			@include media-breakpoint-up(lg){
				margin-bottom:0;
			}
		}
	}
	&__map{
		border-radius: 15px;
		height:160px;
		width: 100%;
		padding: 0;
		@include media-breakpoint-up(lg){
			height:375px;
		}
	}
	&__line-bottom{
		margin-bottom: 25px;
		border-bottom: 0.1px solid #000;
	}
	.email a{
		text-decoration: underline;
	}
}